export default{
	data(){
		return{
			msgDetail:{}
		}
	},
	onLoad(e){
		let msgDetail=uni.getStorageSync("msgdetails")
		this.msgDetail=JSON.parse(msgDetail)
		this.init()
	},
	methods:{
		init(){
			this.$http.post("puNews/updateState",{
				id:this.msgDetail.id,
				state:1,
			}).then(res=>{
				if(res.code!=100){
					uni.showToast({
						icon:"none",
						title:res.msg
					})
				}
			})
		}
	}
}
